import React from 'react';
import MenuBar from '../components/menuBar/MenuBar';
import Modal from '../components/modals/Modal';
import Add_Products from '../components/modals/add_products/Add_Product';





const Products = () => {
  var modal_content = <Modal/>;
  
  return (
    <div className='product-page-container'>
      <MenuBar />
      <div id='add_product_contaienr' style={{ display: "block" }}>
        {modal_content}
      </div>
      <div className="bg-light rounded-3 m-2 p-2">

        <div className="row m-3 p-2 shadow ">
          <form className='col-6 row' action="">
            <div className="col-9 p-0">
              <input className='form-control' type="text" name="" id="" placeholder='Enter to search' />
            </div>
            <div className="col-3 p-0">
              <input className='form-control' type="submit" value="submit" />
            </div>
          </form>
          <div className="col-6 d-flex justify-content-end gap-2">
            <span type='button' className='btn btn-primary' id='add_product' onClick={modal_content = <Add_Products/>}>Add Product</span>
            <button className='btn btn-primary' type="button">Add Catagory</button>
          </div>
        </div>

        <div className="">
          <table>
            <thead>
              <th>ID</th>
              <th>Name</th>
              <th>Catagory</th>
              <th>Stocks</th>
              <th>Price</th>
            </thead>
            <tbody>
              <tr>
                <td>id</td>
                <td>name</td>
                <td>catagory</td>
                <td>stocks</td>
                <td>price</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}
export default Products;

