import React, { useState, useEffect } from 'react';
import { CheckAuth } from '../config/Login';
import { auth } from '../config/firebase';
import { linkWithPopup, GoogleAuthProvider, sendEmailVerification,applyActionCode } from 'firebase/auth';
import { getParameterByName } from '../utils/Utils';
import { toast } from 'react-toastify';
const Dashboard = () => {
  const { userDetails, isVerified, isLoading } = CheckAuth();
  const [state, setstate] = useState(false);
  useEffect(() => {
    const data = auth;
    setstate(data);
  }, []);
  if (isLoading) {
    return (
      <div className="text-center" >Loading...</div>
    )
  }


  if (state) {
    // TODO: Implement getParameterByName()

    // Get the action to complete.
    const mode = getParameterByName('mode');
    // console.log(mode);
    // Get the one-time code from the query parameter.
    const actionCode = getParameterByName('oobCode');
    // console.log(actionCode);
    // (Optional) Get the continue URL from the query parameter if available.
    const continueUrl = getParameterByName('continueUrl');
    // console.log(continueUrl);
    // (Optional) Get the language code if available.
    const lang = getParameterByName('lang') || 'en';
    // console.log(lang);

    // Configure the Firebase SDK.
    // This is the minimum configuration required for the API to be used.

    // Handle the user management action.
    switch (mode) {
      case 'resetPassword':
        // Display reset password handler and UI.
        // handleResetPassword(auth, actionCode, continueUrl, lang);
        break;
      case 'recoverEmail':
        // Display email recovery handler and UI.
        // handleRecoverEmail(auth, actionCode, lang);
        break;
      case 'verifyEmail':
        // Display email verification handler and UI.
        handleVerifyEmail(state, actionCode, continueUrl, lang);
        break;
      default:
    }
  } else {
    return (
      <div className="text-center" >Loading...</div>
    )
  }
  function handleVerifyEmail(s, actionCode, continueUrl, lang) {
    applyActionCode(s, actionCode).then((resp) => {
      console.log(resp);
      toast.success(
        "Verified",{
          position: "bottom-center",
        }
      );
    }).catch((error) => {
      toast.error(error.message, {
        position: "bottom-center",
      });
      console.log(error)
    });
  }

  const doWork = () => {
    const data = sendEmailVerification(auth.currentUser)
      .then((result) => {
        // The link was successfully sent. Inform the user.
        // Save the email locally so you don't need to ask the user for it again
        // if they open the link on the same device.
        console.log(result);
        // ...
      })
      .catch((error) => {
        const errorCode = error.code;
        const errorMessage = error.message;
        console.log(error);
        // ...
      });
    console.log(data);
  }
  if (userDetails) {
    return (
      <div className="text-center" onClick={doWork}>This is User {userDetails.email}</div>
    )
  }
}
export default Dashboard;
