import React from 'react';
import { CheckAuth, signOut } from '../config/Login';
import { sendEmailVerification, linkWithPopup } from 'firebase/auth';
import { auth } from '../config/firebase';
function Otp() {
  const { userDetails, isVerified, isLoading } = CheckAuth();
  let email = '';
  if (isLoading) {
    return (
      <div className="text-center" >Loading...</div>
    )
  } else {
    const [localPart, domainPart] = userDetails.email.split('@');
    const firstTwo = localPart.slice(0, 2);
    const lastTwo = localPart.slice(-2);
    const masked = firstTwo + '****' + lastTwo;
    email = masked + '@' + domainPart;
  }
  const sendEmail= ()=>{
    sendEmailVerification(auth.currentUser)
  }
  // const linkProvider = () =>{
  //   linkWithPopup(auth.currentUser, provider).then((result) => {
  //     // Accounts successfully linked.
  //     const credential = GoogleAuthProvider.credentialFromResult(result);
  //     const user = result.user;
  //     // ...
  //   }).catch((error) => {
  //     // Handle Errors here.
  //     // ...
  //   });
  // }
  return (
    <div className='vh-100 d-flex justify-content-center align-items-center'>
      <div className="bg-light rounded-3 p-4">
        <h1 className='text-center h1'>OTP Verification</h1>
        <div className="d-flex justify-content-center align-items-center flex-column">
          <apan>The OTP has been sent to your Email</apan>
          <span className='fw-bold'>{email}</span>
        </div>

        <form >
          <div className="text-center d-flex flex-column justify-content-center align-items-center">
            <input className='form-control text-center m-3' type="number" maxLength={6} name="" id="" placeholder='OTP' required />
            <input className='btn btn-success m-2' type="button" value="Submit" onClick={sendEmail}/>
            <input className='btn btn-danger m-2' type="button" value="Logout" onClick={signOut}/>
          </div>
        </form>
      </div>
    </div>
  );
}

export default Otp;
