import React, { useState } from 'react';
import logo from "../logo.svg";
import google_logo from '../asset/img/google-logo.png'
import { CheckAuth } from '../config/Login';
import { GoogleAuthProvider, signInWithPopup, signInWithEmailAndPassword } from 'firebase/auth';
import { toast } from "react-toastify";
import { auth } from '../config/firebase';


function Login() {
    const { userDetails, isVerified, isLoading } = CheckAuth();
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    if (isLoading) {
        return (
            <div className="text-center" >Loading...</div>
        )
    }
    const LoginWithGoogle = () => {
        const provider = new GoogleAuthProvider();
        signInWithPopup(auth, provider);
    }
    const LoginWithEmailAndPassword = async (e) => {
        e.preventDefault();
        try {
            await signInWithEmailAndPassword(auth, email, password);
            console.log("User logged in Successfully");
            window.location.href = "/profile";
            toast.success("User logged in Successfully", {
                position: "top-center",
            });
        } catch (error) {
            console.log(error.message);

            toast.error(error.message, {
                position: "bottom-center",
            });
        }
    }

    return (
        <div class="bg-secondary vh-100 d-flex justify-content-center align-items-center">
            <div class="bg-light px-3 py-5 rounded-3 shadow w-75">
                <form class="row d-flex justify-content-center align-items-center"
                    onSubmit={LoginWithEmailAndPassword}
                >
                    <div class="col-md-12">
                        <h1 class="text-center">Admin Login</h1>
                    </div>
                    <div class="col-md-6">
                        <img class="img-fluid h-100 w-100" src={logo} alt="Logo" />
                    </div>
                    <div class="row col-md-6">
                        <div class="col-md-12 g-2">
                            <div className="d-flex gap-2 py-1 shadow cursor-pointer justify-content-center align-items-center rounded-3 border-dark border"
                                onClick={LoginWithGoogle}
                            >
                                <img className='img-fluid google-logo' src={google_logo} alt="" />
                                <h5 className='text-center'>Sign in with <b>Google</b></h5>
                            </div>
                        </div>
                        <div class="col-md-12 g-2">
                            <label for="username" class="form-label">Username</label>
                            <input class="form-control" type="text" id="username" value={email}
                                onChange={(e) => setEmail(e.target.value)} />
                        </div>
                        <div class="col-md-12 g-2">
                            <label for="password" class="form-label">Password</label>
                            <div class="input-group">
                                <input class="form-control" type="password" id="password" value={password}
                                    onChange={(e) => setPassword(e.target.value)} />
                                <span class="input-group-text material-symbols-outlined cursor-pointer" id="visibisity_btn" >visibility_off</span>
                            </div>
                        </div>
                        <div class="col-md-12 g-2">
                            <input class="btn btn-success" type="submit" id="login" name="login" value={"Login"} />
                        </div>
                    </div>
                </form>
            </div>
        </div>

    );
}

export default Login;